import React, {Component} from 'react';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import DetailItem from 'components/itemDetail/DetailItem';
import * as itemActions from 'store/modules/item';
import * as cartActions from 'store/modules/cart';
import { withRouter } from 'react-router-dom';
class ItemDetailContainer extends Component {

  getItemById = async () => {
    const { ItemActions, id } = this.props;
    try {
      await ItemActions.getItemById(id);
    } catch(e){
      console.log(e);
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if(prevProps.price !== this.props.price) {
      this.initializeEachItemTotalPrice({eachItemPrice: this.props.price});
    }
  }

  initializeEachItemTotalPrice = ({eachItemPrice}) => {
    const { CartActions } = this.props;
    CartActions.initializeEachItemTotalPrice({eachItemPrice});
  }

  componentDidMount() {
    const { CartActions } = this.props;
    this.getItemById();
    CartActions.initialize();

  }

  handleChangePriceBySelection = ({price}) => {
    const { CartActions } = this.props;
    CartActions.changeBySelection({price});
    // const { ItemActions } = this.props;
    // ItemActions.changePriceBySelection({price});
  }


  handleChangeAmount = ({value, price}) => {
    const { CartActions } = this.props;
    CartActions.changeAmount({value, price});
  }


  addCartList = async () => {
    const { CartActions, id, title, amount, imageNames, eachItemTotalPrice } = this.props;
    const thumbnailImage = imageNames.split(',').slice(0, this.props.imageNames.split(',').length - 1)[0];

    try {
       await CartActions.addCartList({id, title, amount, thumbnailImage, totalPrice: eachItemTotalPrice});
    } catch(e) {
      console.log(e);
    }
  }

  resetCartLog = () => {
    const { CartActions } = this.props;
    CartActions.resetCartLog();
  }

  removeItemById = async () => {
    const { ItemActions, history, itemId } = this.props;

    try {
      await ItemActions.removeItemById(itemId);
      alert("아이템이 삭제되었습니다.");
      history.push("/list");
    } catch(e) {
      console.log(e);
    }
  }



  
  render() {
    const { itemId, 
            title, 
            body, 
            price, 
            imageNames, 
            publishedDate, 
            amount, 
            eachItemTotalPrice, 
            cartLog,
            errorCode,
            errorLog,
            logged } = this.props;
    const { handleChangePriceBySelection, handleChangeAmount, addCartList, resetCartLog, removeItemById, updateItemById } = this;
    return (<DetailItem 
              amount={amount}
              id={itemId}
              title={title}
              body={body}
              price={price}
              imageNames={imageNames}
              publishedDate={publishedDate}
              onChangePriceBySelection={handleChangePriceBySelection}
              onChangeAmount={handleChangeAmount}
              eachItemTotalPrice={eachItemTotalPrice}
              addCartList={addCartList}
              cartLog={cartLog}
              onResetCartLog={resetCartLog}
              errorCode={errorCode}
              errorLog={errorLog}
              logged={logged}
              onRemoveItemById={removeItemById}/>);
  }
}

export default connect((state) => ({
  itemId: state.item.getIn(['item', 'id']),
  title: state.item.getIn(['item', 'title']), 
  body: state.item.getIn(['item', 'body']),
  price: state.item.getIn(['item', 'price']), 
  imageNames: state.item.getIn(['item', 'imageNames']), 
  publishedDate: state.item.getIn(['item', 'publishedDate']),
  amount: state.cart.get('amount'),
  eachItemTotalPrice: state.cart.get('eachItemTotalPrice'),
  cartLog: state.cart.get('cartLog'),
  errorCode: state.cart.getIn(['error', 'errorCode']),
  errorLog: state.cart.getIn(['error', 'errorLog']),
  logged: state.base.get('logged')
}), (dispatch) => ({
  ItemActions: bindActionCreators(itemActions, dispatch),
  CartActions: bindActionCreators(cartActions, dispatch)
}))(withRouter(ItemDetailContainer));


